.heading {
  text-align: center;
  margin: 10px 0 80px 0;
  font-size: 35px;
  &.small {
    font-size: 14px;
  }
  &.medium {
    font-size: 24px;
    margin-top: 40px;
    margin-bottom: 50px;
    text-align: left;
    margin-left: 10px;
  }
  &.big {
    font-size: 35px;
  }
  .content {
    display: inline-block;
    position: relative;
    font-weight: bold;
    &:after {
      content: '';
      position: absolute;
      bottom: -10px;
      left: -10px;
      width: calc(100% + 20px);
      height: 2px;
      background: #000000;
    }
  }
}