.service {
  max-width: 1300px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d7d7d7;
  align-items: center;
  &__name {
    flex-grow: 1;
    line-height: 23px;
    font-weight: bold;
  }
  &__price {
    flex: none;
    width: 120px;
    text-align: right;
    font-weight: bold;
  }
}