.header {
  width: 100%;
  margin: 0 auto;
  padding: 10px 20px;
  height: 67px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background: #fff;
  -webkit-box-shadow: 0 0 13px -4px rgba(150,150,150,1);
  -moz-box-shadow: 0 0 13px -4px rgba(150,150,150,1);
  box-shadow: 0 0 13px -4px rgba(150,150,150,1);
  display: flex;
  align-items: center;

  .avatar {
    width: 50px;
    height: 50px;
    display: inline-block;
    border-radius: 100%;
    background-size: cover;
    margin-right: 20px;
  }
  .name {
    padding: 10px 0;
    margin-right: 20px;
    display: inline-flex;
    align-items: center;
  }
}