.footer {
  max-width: 1300px;
  padding: 30px;
  border-top: 1px solid #e0e0e0;
  margin: 200px auto 0;
  .userlink {
    display: inline-block;
    margin-left: 7px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background: #2b2b2b;
    }
  }
}